"use client";
import { useEffect, useRef, useCallback } from "react";

export const ScrollStackItem = ({ children, itemClassName = "" }) => (
  <div
    className={`scroll-stack-card w-11/12 h-[700px] p-8 rounded-[40px] shadow-2xl ${itemClassName}`.trim()}
  >
    {children}
  </div>
);

const ScrollStack = ({
  children,
  className = "",
  stackGap = 20,
}) => {
  const containerRef = useRef(null);
  const cardsRef = useRef([]);
  const lastScrollTop = useRef(0);

  const updateCardTransforms = useCallback(() => {
    if (!cardsRef.current.length) return;

    const scrollTop = window.scrollY;
    const viewportHeight = window.innerHeight;
    const stickyTop = viewportHeight * 0.1;

    cardsRef.current.forEach((card, index) => {
      if (!card) return;

      const cardOffsetTop = card.offsetTop;
      const container = containerRef.current;
      const containerTop = container.getBoundingClientRect().top + scrollTop;
      
      // Calculate the target sticky position including the gap for this card
      const targetStickyPosition = stickyTop + (index * stackGap);
      
      // When should this card start being sticky? 
      // Subtract the gap so it reaches its final position smoothly
      const stickyStart = containerTop + cardOffsetTop - targetStickyPosition;
      
      // When should this card stop being sticky (when last card arrives)?
      const lastCard = cardsRef.current[cardsRef.current.length - 1];
      const lastCardOffsetTop = lastCard.offsetTop;
      const lastTargetPosition = stickyTop + ((cardsRef.current.length - 1) * stackGap);
      const stickyEnd = containerTop + lastCardOffsetTop - lastTargetPosition;

      let translateY = 0;
      const scale = 1; // All cards same size

      if (scrollTop >= stickyStart && scrollTop <= stickyEnd) {
        // Card is sticky - pin it at the exact target position
        translateY = scrollTop - (containerTop + cardOffsetTop) + targetStickyPosition;
      } else if (scrollTop > stickyEnd) {
        // All cards scroll away together
        translateY = stickyEnd - (containerTop + cardOffsetTop) + targetStickyPosition;
      }

      // Round to avoid sub-pixel jittering
      translateY = Math.round(translateY);

      card.style.transform = `translate3d(0, ${translateY}px, 0) scale(${scale})`;
      card.style.transformOrigin = 'top center';
      card.style.position = 'relative';
      card.style.zIndex = index + 1;
      card.style.willChange = 'transform';
    });

    lastScrollTop.current = scrollTop;
  }, [stackGap]);

  const handleScroll = useCallback(() => {
    requestAnimationFrame(() => {
      updateCardTransforms();
    });
  }, [updateCardTransforms]);

  useEffect(() => {
    const container = containerRef.current;
    if (!container) return;

    const cards = Array.from(container.querySelectorAll(".scroll-stack-card"));
    cardsRef.current = cards;

    window.addEventListener("scroll", handleScroll, { passive: true });
    window.addEventListener("resize", updateCardTransforms, { passive: true });
    
    // Initial update
    requestAnimationFrame(() => {
      updateCardTransforms();
    });

    return () => {
      window.removeEventListener("scroll", handleScroll);
      window.removeEventListener("resize", updateCardTransforms);
    };
  }, [handleScroll, updateCardTransforms]);

  return (
    <div
      ref={containerRef}
      className={`relative w-full ${className}`.trim()}
    >
      <div className="w-11/12 mx-auto flex flex-col items-center gap-10 px-4 md:px-20 p-8">
        {children}
      </div>
    </div>
  );
};

// Demo component
export default function ScrollStackDemo() {
  return (
    <div className="w-full bg-gray-100">
      
      <ScrollStack>
        <ScrollStackItem itemClassName="bg-blue-500 text-white">
          <div className="flex flex-col justify-center h-full">
            <h2 className="text-4xl font-bold mb-6">Card 1 - Top Position</h2>
            <p className="text-lg opacity-90">I stick first at the top position</p>
          </div>
        </ScrollStackItem>
        
        <ScrollStackItem itemClassName="bg-purple-500 text-white">
          <div className="flex flex-col justify-center h-full">
            <h2 className="text-4xl font-bold mb-6">Card 2</h2>
            <p className="text-lg opacity-90">I stack on top of Card 1, but 40px below it</p>
          </div>
        </ScrollStackItem>
        
        <ScrollStackItem itemClassName="bg-pink-500 text-white">
          <div className="flex flex-col justify-center h-full">
            <h2 className="text-4xl font-bold mb-6">Card 3</h2>
            <p className="text-lg opacity-90">I stack on top of Card 2, but 40px below it</p>
          </div>
        </ScrollStackItem>
        
        <ScrollStackItem itemClassName="bg-purple-600 text-white">
          <div className="flex flex-col justify-center h-full">
            <h2 className="text-4xl font-bold mb-6">Card 4</h2>
            <p className="text-lg opacity-90">I'm on top of the stack, 40px below Card 3</p>
          </div>
        </ScrollStackItem>
      </ScrollStack>
      
    </div>
  );
}