"use client";
import React, { useState, useEffect } from "react";
import { ArrowRight } from "lucide-react";
import ImageTrail from "../components/ImageTrail";
import Link from "next/link";
import CircularText from "../components/CircularText";
import SplitMenu from "../components/SplitMenu";
// Hero Section
const HeroSection = () => {
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const checkMobile = () => {
      setIsMobile(window.innerWidth < 1024);
    };
    checkMobile();
    window.addEventListener("resize", checkMobile);
    return () => window.removeEventListener("resize", checkMobile);
  }, []);

  return (
    <section id="home" className="relative h-screen overflow-hidden ">
      {/* Gradient Background for Mobile */}
      {isMobile && (
        <div className="absolute inset-0 bg-radial from-[#552DCC] via-[#402099] to-[#402099] z-1" />
      )}

      {/* Image Trail - Desktop Only */}
      {!isMobile && (
        <ImageTrail
          images={[
            "/heroImages/1.png",
            "/heroImages/2.jpg",
            "/heroImages/3.jpg",
            "/heroImages/4.jpg",
          ]}
        />
      )}

      {/* Dark Overlay for Text Readability - Desktop Only */}
      {!isMobile && (
        <div className="absolute inset-0 bg-radial from-[#552DCC] via-[#402099] to-[#612099] z-1" />
      )}

      {/* Hero Content - Always on top */}
      <div className="absolute inset-0 flex items-center justify-center z-20 pointer-events-none">
        <div className="  max-w-8/12 mx-auto px-4 sm:px-6 lg:px-8 pointer-events-auto">
          <h1 className="text-4xl sm:text-5xl md:text-6xl lg:text-8xl text-center font-bold text-white mb-6 leading-none drop-shadow-2xl">
            Best Printing Services in Afghanistan. We Print What You Think
          </h1>
          <p className="text-lg sm:text-xl md:text-2xl text-white/90 lg:w-11/12   mx-auto my-10 drop-shadow-xl leading-7">
            Your trusted partner for professional printing services in
            Afghanistan. From brochures to books, business cards to banners, we
            deliver excellence in every print.
          </p>
          <div className="flex flex-col sm:flex-row gap-4 justify-center">
            <Link
              href="/contact"
              className="bg-white text-blue-600 px-12 py-4 rounded-full font-bold text-lg hover:bg-blue-50 transform hover:scale-105 transition-all inline-flex items-center justify-center shadow-xl"
            >
              Get a Quote <ArrowRight className="ml-2" />
            </Link>
            <Link
              href="/portfolio"
              className="border-2 border-white text-white px-12 py-4 rounded-full font-bold text-lg hover:bg-white hover:text-blue-600 transition-all inline-flex items-center justify-center shadow-xl backdrop-blur-sm bg-white/10"
            >
              View Our Work
            </Link>
          </div>
        </div>
      </div>

      {/* Circular Text - Bottom Left */}
      <div className="absolute bottom-16 left-8 z-20 pointer-events-auto">
        <CircularText
          text="STRENGTH INNOVATION KABUL "
          onHover="speedUp"
          spinDuration={20}
          className="custom-class"
        />
      </div>
      <div className="absolute bottom-16 right-8 z-20 pointer-events-auto border-[3px] border-orange-400 rounded-2xl p-4">
        <SplitMenu />
      </div>
    </section>
  );
};

export default HeroSection;
